import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import systemintroList from '@/views/pages/systemintro/list'
import systemintroDetail from '@/views/pages/systemintro/formModel'
import systemintroAdd from '@/views/pages/systemintro/formAdd'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import minsuleixingList from '@/views/pages/minsuleixing/list'
import minsuleixingDetail from '@/views/pages/minsuleixing/formModel'
import minsuleixingAdd from '@/views/pages/minsuleixing/formAdd'
import minsuxinxiList from '@/views/pages/minsuxinxi/list'
import minsuxinxiDetail from '@/views/pages/minsuxinxi/formModel'
import minsuxinxiAdd from '@/views/pages/minsuxinxi/formAdd'
import storeupList from '@/views/pages/storeup/list'
import minsuyudingList from '@/views/pages/minsuyuding/list'
import minsuyudingDetail from '@/views/pages/minsuyuding/formModel'
import minsuyudingAdd from '@/views/pages/minsuyuding/formAdd'
import shangjiaList from '@/views/pages/shangjia/list'
import shangjiaDetail from '@/views/pages/shangjia/formModel'
import shangjiaAdd from '@/views/pages/shangjia/formAdd'
import newsList from '@/views/pages/news/list'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'systemintroList',
			component: systemintroList
		}, {
			path: 'systemintroDetail',
			component: systemintroDetail
		}, {
			path: 'systemintroAdd',
			component: systemintroAdd
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'minsuleixingList',
			component: minsuleixingList
		}, {
			path: 'minsuleixingDetail',
			component: minsuleixingDetail
		}, {
			path: 'minsuleixingAdd',
			component: minsuleixingAdd
		}
		, {
			path: 'minsuxinxiList',
			component: minsuxinxiList
		}, {
			path: 'minsuxinxiDetail',
			component: minsuxinxiDetail
		}, {
			path: 'minsuxinxiAdd',
			component: minsuxinxiAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'minsuyudingList',
			component: minsuyudingList
		}, {
			path: 'minsuyudingDetail',
			component: minsuyudingDetail
		}, {
			path: 'minsuyudingAdd',
			component: minsuyudingAdd
		}
		, {
			path: 'shangjiaList',
			component: shangjiaList
		}, {
			path: 'shangjiaDetail',
			component: shangjiaDetail
		}, {
			path: 'shangjiaAdd',
			component: shangjiaAdd
		}
		, {
			path: 'newsList',
			component: newsList
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
