
<template>
	<div class="app-contain" :style='{"minHeight":"100vh","padding":"0","margin":"20px auto 60px","borderRadius":"0px","background":"#fff","width":"80%","position":"relative","height":"100%"}'>
		<div class="bread_view">
			<el-breadcrumb separator=">" class="breadcrumb">
				<el-breadcrumb-item class="first_breadcrumb" :to="{ path: '/' }">首页</el-breadcrumb-item>
				<el-breadcrumb-item class="second_breadcrumb" v-for="(item,index) in breadList" :key="index">{{item.name}}</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<el-form ref="formRef" :model="form" class="add_form" label-width="120px" :rules="rules">
			<el-row>
				<el-col :span="13">
					<el-form-item label="房间名称" prop="fangjianmingcheng">
						<el-input class="list_inp" v-model="form.fangjianmingcheng" placeholder="房间名称"
							 type="text" 							:readonly="!isAdd||disabledForm.fangjianmingcheng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="房间类型" prop="fangjianleixing">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.fangjianleixing?true:false"
							v-model="form.fangjianleixing" 
							placeholder="请选择房间类型"
							style="width:100%;"
							>
							<el-option v-for="(item,index) in fangjianleixingLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="13">
					<el-form-item label="一晚价格" prop="yiwanjiage">
						<el-input class="list_inp" v-model.number="form.yiwanjiage" placeholder="一晚价格"
							 type="number" 							:readonly="!isAdd||disabledForm.yiwanjiage?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="民宿地址" prop="minsudizhi">
						<el-input class="list_inp" v-model="form.minsudizhi" placeholder="民宿地址"
							 type="text" 							:readonly="!isAdd||disabledForm.minsudizhi?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="民宿类型" prop="minsuleixing">
						<el-input class="list_inp" v-model="form.minsuleixing" placeholder="民宿类型"
							 type="text" 							:readonly="!isAdd||disabledForm.minsuleixing?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="入住日期" prop="ruzhuriqi">
						<el-date-picker
							class="list_date"
							v-model="form.ruzhuriqi"
							format="YYYY 年 MM 月 DD 日"
							value-format="YYYY-MM-DD"
							type="datetime"
							:readonly="!isAdd||disabledForm.ruzhuriqi?true:false"
							placeholder="请选择入住日期"
							style="width: 100%;" />
					</el-form-item>
				</el-col>
				<el-col :span="13">
					<el-form-item label="入住天数" prop="ruzhutianshu">
						<el-input class="list_inp" v-model.number="form.ruzhutianshu" placeholder="入住天数"
							 type="text" 							:readonly="!isAdd||disabledForm.ruzhutianshu?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="入住金额" prop="ruzhujine">
						<el-input class="list_inp" v-model="ruzhujine" placeholder="入住金额" readonly></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="13">
					<el-form-item label="预订时间" prop="yudingshijian">
						<el-date-picker
							class="list_date"
							v-model="form.yudingshijian"
							format="YYYY-MM-DD HH:mm:ss"
							value-format="YYYY-MM-DD HH:mm:ss"
							type="datetime"
							style="width:100%;"
							:readonly="!isAdd||disabledForm.yudingshijian?true:false"
							placeholder="请选择预订时间" />
					</el-form-item>
				</el-col>
				<el-col :span="13">
					<el-form-item label="用户账号" prop="yonghuzhanghao">
						<el-input class="list_inp" v-model="form.yonghuzhanghao" placeholder="用户账号"
							 type="text" 							:readonly="!isAdd||disabledForm.yonghuzhanghao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="用户姓名" prop="yonghuxingming">
						<el-input class="list_inp" v-model="form.yonghuxingming" placeholder="用户姓名"
							 type="text" 							:readonly="!isAdd||disabledForm.yonghuxingming?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="手机号码" prop="shoujihaoma">
						<el-input class="list_inp" v-model="form.shoujihaoma" placeholder="手机号码"
							 type="text" 							:readonly="!isAdd||disabledForm.shoujihaoma?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="商家账号" prop="shangjiazhanghao">
						<el-input class="list_inp" v-model="form.shangjiazhanghao" placeholder="商家账号"
							 type="text" 							:readonly="!isAdd||disabledForm.shangjiazhanghao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="商家名称" prop="shangjiamingcheng">
						<el-input class="list_inp" v-model="form.shangjiamingcheng" placeholder="商家名称"
							 type="text" 							:readonly="!isAdd||disabledForm.shangjiamingcheng?true:false" />
					</el-form-item>
				</el-col>

			</el-row>
			<div class="formModel_btn_box">
				<el-button class="formModel_cancel" @click="backClick">取消</el-button>
				<el-button class="formModel_confirm" @click="save"
					type="success"
					>
					保存
				</el-button>
			</div>
		</el-form>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		watch,
		onUnmounted,
		onMounted,
		nextTick,
		computed
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const route = useRoute()
	const router = useRouter()
	//基础信息
	const tableName = 'minsuyuding'
	const formName = '民宿预订'
	//基础信息
	const breadList = ref([{
		name: formName
	}])
	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//form表单
	const form = ref({
		fangjianmingcheng: '',
		fangjianleixing: '',
		yiwanjiage: '',
		minsudizhi: '',
		minsuleixing: '',
		ruzhuriqi: '',
		ruzhutianshu: '',
		ruzhujine: '',
		yudingshijian: '',
		yonghuzhanghao: '',
		yonghuxingming: '',
		shoujihaoma: '',
		sfsh: '待审核',
		shhf: '',
		ispay: '未支付',
		shangjiazhanghao: '',
		shangjiamingcheng: '',
	})
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const disabledForm = ref({
		fangjianmingcheng : false,
		fangjianleixing : false,
		yiwanjiage : false,
		minsudizhi : false,
		minsuleixing : false,
		ruzhuriqi : false,
		ruzhutianshu : false,
		ruzhujine : false,
		yudingshijian : false,
		yonghuzhanghao : false,
		yonghuxingming : false,
		shoujihaoma : false,
		sfsh : false,
		shhf : false,
		ispay : false,
		shangjiazhanghao : false,
		shangjiamingcheng : false,
	})
	const isAdd = ref(false)
	//表单验证
	//匹配整数
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error("请输入整数"));
		} else {
			callback();
		}
	}
	//匹配数字
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error("请输入数字"));
		} else {
			callback();
		}
	}
	//匹配手机号码
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error("请输入正确的手机号码"));
		} else {
			callback();
		}
	}
	//匹配电话号码
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error("请输入正确的电话号码"));
		} else {
			callback();
		}
	}
	//匹配邮箱
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error("请输入正确的邮箱地址"));
		} else {
			callback();
		}
	}
	//匹配身份证
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error("请输入正确的身份证号码"));
		} else {
			callback();
		}
	}
	//匹配网站地址
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("请输入正确的URL地址"));
		} else {
			callback();
		}
	}
	const rules = ref({
		fangjianmingcheng: [
		],
		fangjianleixing: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		yiwanjiage: [
			{required: true,message: '请输入',trigger: 'blur'}, 
			{ validator: validateNumber, trigger: 'blur' },
		],
		minsudizhi: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		minsuleixing: [
		],
		ruzhuriqi: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		ruzhutianshu: [
			{required: true,message: '请输入',trigger: 'blur'}, 
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		ruzhujine: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		yudingshijian: [
		],
		yonghuzhanghao: [
		],
		yonghuxingming: [
		],
		shoujihaoma: [
		],
		sfsh: [
		],
		shhf: [
		],
		ispay: [
		],
		shangjiazhanghao: [
		],
		shangjiamingcheng: [
		],
	})
	//房间类型列表
	const fangjianleixingLists = ref([])
	const ruzhujine =computed(()=>{
		let c = form.value
		let a = c.yiwanjiage*c.ruzhutianshu
		form.value.ruzhujine = a?a.toFixed(2):0
		return a?a.toFixed(2):0
	})
	//methods

	//methods
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init = (formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null) => {
			form.value.yudingshijian = context?.$toolUtil.getCurDateTime()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			// getInfo()
			for(let x in row){
				if(x=='fangjianmingcheng'){
					form.value.fangjianmingcheng = row[x];
					disabledForm.value.fangjianmingcheng = true;
					continue;
				}
				if(x=='fangjianleixing'){
					form.value.fangjianleixing = row[x];
					disabledForm.value.fangjianleixing = true;
					continue;
				}
				if(x=='yiwanjiage'){
					form.value.yiwanjiage = row[x];
					disabledForm.value.yiwanjiage = true;
					continue;
				}
				if(x=='minsudizhi'){
					form.value.minsudizhi = row[x];
					disabledForm.value.minsudizhi = true;
					continue;
				}
				if(x=='minsuleixing'){
					form.value.minsuleixing = row[x];
					disabledForm.value.minsuleixing = true;
					continue;
				}
				if(x=='ruzhuriqi'){
					form.value.ruzhuriqi = row[x];
					disabledForm.value.ruzhuriqi = true;
					continue;
				}
				if(x=='ruzhutianshu'){
					form.value.ruzhutianshu = row[x];
					disabledForm.value.ruzhutianshu = true;
					continue;
				}
				if(x=='ruzhujine'){
					form.value.ruzhujine = row[x];
					disabledForm.value.ruzhujine = true;
					continue;
				}
				if(x=='yudingshijian'){
					form.value.yudingshijian = row[x];
					disabledForm.value.yudingshijian = true;
					continue;
				}
				if(x=='yonghuzhanghao'){
					form.value.yonghuzhanghao = row[x];
					disabledForm.value.yonghuzhanghao = true;
					continue;
				}
				if(x=='yonghuxingming'){
					form.value.yonghuxingming = row[x];
					disabledForm.value.yonghuxingming = true;
					continue;
				}
				if(x=='shoujihaoma'){
					form.value.shoujihaoma = row[x];
					disabledForm.value.shoujihaoma = true;
					continue;
				}
				if(x=='shangjiazhanghao'){
					form.value.shangjiazhanghao = row[x];
					disabledForm.value.shangjiazhanghao = true;
					continue;
				}
				if(x=='shangjiamingcheng'){
					form.value.shangjiamingcheng = row[x];
					disabledForm.value.shangjiamingcheng = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			form.value.sfsh='待审核'
			form.value.ispay='未支付'
		}
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(context?.$toolUtil.storageGet("frontRole")!="管理员") {
				disabledForm.value.yudingshijian = true;
			}
			if(json.hasOwnProperty('yonghuzhanghao') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.yonghuzhanghao = json.yonghuzhanghao
				disabledForm.value.yonghuzhanghao = true;
			}
			if(json.hasOwnProperty('yonghuxingming') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.yonghuxingming = json.yonghuxingming
				disabledForm.value.yonghuxingming = true;
			}
			if(json.hasOwnProperty('shoujihaoma') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.shoujihaoma = json.shoujihaoma
				disabledForm.value.shoujihaoma = true;
			}
			if(json.hasOwnProperty('shangjiazhanghao') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.shangjiazhanghao = json.shangjiazhanghao
				disabledForm.value.shangjiazhanghao = true;
			}
			if(json.hasOwnProperty('shangjiamingcheng') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.shangjiamingcheng = json.shangjiamingcheng
				disabledForm.value.shangjiamingcheng = true;
			}
		})
		fangjianleixingLists.value = "单人间,大床房,双人间,三人间,标准套间,豪华套间".split(',')
	}
	//初始化
	//取消
	const backClick = () => {
		history.back()
	}
	//提交
	const save=()=>{
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(`操作成功`,'success',()=>{
									history.back()
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(`操作成功`,'success',()=>{
							history.back()
						})
					})
				}
			}
		})
	}
	//修改跨表数据
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
	onMounted(()=>{
		type.value = route.query.type?route.query.type:'add'
		let row = null
		let table = null
		let statusColumnName = null
		let tips = null
		let statusColumnValue = null
		if(type.value == 'cross'){
			row = context?.$toolUtil.storageGet('crossObj')?JSON.parse(context?.$toolUtil.storageGet('crossObj')):{}
			table = context?.$toolUtil.storageGet('crossTable')
			statusColumnName = context?.$toolUtil.storageGet('crossStatusColumnName')
			tips = context?.$toolUtil.storageGet('crossTips')
			statusColumnValue = context?.$toolUtil.storageGet('crossStatusColumnValue')
		}
		init(route.query.id?route.query.id:null, type.value,'', row, table, statusColumnName, tips, statusColumnValue)
	})
	
</script>
<style lang="scss" scoped>
	// 面包屑盒子
	.bread_view {
		border-radius: 0px;
		padding: 12px 20px;
		margin: 0px auto;
		background: none;
		width: 100%;
		border-color: #eee;
		border-width: 0 0 0px;
		position: relative;
		border-style: solid;
		:deep(.breadcrumb) {
			font-size: 14px;
			line-height: 1;
			.el-breadcrumb__separator {
				margin: 0 9px;
				color: #999;
				font-weight: 500;
			}
			.first_breadcrumb {
				.el-breadcrumb__inner {
					color: #333;
					display: inline-block;
				}
			}
			.second_breadcrumb {
				.el-breadcrumb__inner {
					color: #999;
					display: inline-block;
				}
			}
		}
	}
	// 表单
	.add_form{
		border: 1px solid #B5D0AC20;
		border-radius: 0px;
		padding: 40px 7% 50px;
		background: #B5D0AC10;
		// form item
		:deep(.el-form-item) {
			border: 0px solid #eee;
			padding: 6px 0;
			margin: 0 0 20px 0;
			background: none;
			display: flex;
			//label
			.el-form-item__label {
			 background: none;
			 display: block;
			 width: auto;
			 min-width: 150px;
			 text-align: right;
			}
			// 内容盒子
			.el-form-item__content {
				display: flex;
				width: calc(100% - 150px);
