
<template>
	<div class="app-contain" :style='{"minHeight":"100vh","padding":"0","margin":"20px auto 60px","borderRadius":"0px","background":"#fff","width":"80%","position":"relative","height":"100%"}'>
		<div class="bread_view">
			<el-breadcrumb separator=">" class="breadcrumb">
				<el-breadcrumb-item class="first_breadcrumb" :to="{ path: '/' }">首页</el-breadcrumb-item>
				<el-breadcrumb-item class="second_breadcrumb" v-for="(item,index) in breadList" :key="index">{{item.name}}</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<el-form ref="formRef" :model="form" class="add_form" label-width="120px" :rules="rules">
			<el-row>
				<el-col :span="13">
					<el-form-item label="房间名称" prop="fangjianmingcheng">
						<el-input class="list_inp" v-model="form.fangjianmingcheng" placeholder="房间名称"
							 type="text" 							:readonly="!isAdd||disabledForm.fangjianmingcheng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="房间图片" prop="fangjiantupian">
						<uploads
							:disabled="!isAdd||disabledForm.fangjiantupian?true:false"
							action="file/upload" 
							tip="请上传房间图片" 
							:limit="3" 
							style="width: 100%;text-align: left;"
							:fileUrls="form.fangjiantupian?form.fangjiantupian:''" 
							@change="fangjiantupianUploadSuccess">
						</uploads>
					</el-form-item>
				</el-col>
				<el-col :span="13">
					<el-form-item label="房间类型" prop="fangjianleixing">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.fangjianleixing?true:false"
							v-model="form.fangjianleixing" 
							placeholder="请选择房间类型"
							style="width:100%;"
							>
							<el-option v-for="(item,index) in fangjianleixingLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="13">
					<el-form-item label="一晚价格" prop="yiwanjiage">
						<el-input class="list_inp" v-model.number="form.yiwanjiage" placeholder="一晚价格"
							 type="number" 							:readonly="!isAdd||disabledForm.yiwanjiage?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="服务电话" prop="fuwudianhua">
						<el-input class="list_inp" v-model="form.fuwudianhua" placeholder="服务电话"
							 type="text" 							:readonly="!isAdd||disabledForm.fuwudianhua?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="民宿地址" prop="minsudizhi">
						<el-input class="list_inp" v-model="form.minsudizhi" placeholder="民宿地址"
							 type="text" 							:readonly="!isAdd||disabledForm.minsudizhi?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="民宿类型" prop="minsuleixing">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.minsuleixing?true:false"
							v-model="form.minsuleixing" 
							placeholder="请选择民宿类型"
							style="width:100%;"
							>
							<el-option v-for="(item,index) in minsuleixingLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="13">
					<el-form-item label="商家账号" prop="shangjiazhanghao">
						<el-input class="list_inp" v-model="form.shangjiazhanghao" placeholder="商家账号"
							 type="text" 							:readonly="!isAdd||disabledForm.shangjiazhanghao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="商家名称" prop="shangjiamingcheng">
						<el-input class="list_inp" v-model="form.shangjiamingcheng" placeholder="商家名称"
							 type="text" 							:readonly="!isAdd||disabledForm.shangjiamingcheng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="13">
					<el-form-item label="房源状态" prop="fangyuanzhuangtai">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.fangyuanzhuangtai?true:false"
							v-model="form.fangyuanzhuangtai" 
							placeholder="请选择房源状态"
							style="width:100%;"
							>
							<el-option v-for="(item,index) in fangyuanzhuangtaiLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="房内设施" prop="fangneisheshi">
						<editor class="list_editor" :value="form.fangneisheshi" placeholder="请输入房内设施" :readonly="!isAdd||disabledForm.fangneisheshi?true:false"
							@change="(e)=>editorChange(e,'fangneisheshi')"></editor>
					</el-form-item>
				</el-col>
			</el-row>
			<div class="formModel_btn_box">
				<el-button class="formModel_cancel" @click="backClick">取消</el-button>
				<el-button class="formModel_confirm" @click="save"
					type="success"
					>
					保存
				</el-button>
			</div>
		</el-form>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		watch,
		onUnmounted,
		onMounted,
		nextTick,
		computed
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const route = useRoute()
	const router = useRouter()
	//基础信息
	const tableName = 'minsuxinxi'
	const formName = '民宿信息'
	//基础信息
	const breadList = ref([{
		name: formName
	}])
	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//form表单
	const form = ref({
		fangjianmingcheng: '',
		fangjiantupian: '',
		fangjianleixing: '',
		yiwanjiage: '',
		fuwudianhua: '',
		fangneisheshi: '',
		storeupnum: '0',
		minsudizhi: '',
		minsuleixing: '',
		shangjiazhanghao: '',
		shangjiamingcheng: '',
		fangyuanzhuangtai: '空闲',
	})
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const disabledForm = ref({
		fangjianmingcheng : false,
		fangjiantupian : false,
		fangjianleixing : false,
		yiwanjiage : false,
		fuwudianhua : false,
		fangneisheshi : false,
		storeupnum : false,
		minsudizhi : false,
		minsuleixing : false,
		shangjiazhanghao : false,
		shangjiamingcheng : false,
		fangyuanzhuangtai : false,
	})
	const isAdd = ref(false)
	//表单验证
	//匹配整数
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error("请输入整数"));
		} else {
			callback();
		}
	}
	//匹配数字
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error("请输入数字"));
		} else {
			callback();
		}
	}
	//匹配手机号码
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error("请输入正确的手机号码"));
		} else {
			callback();
		}
	}
	//匹配电话号码
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error("请输入正确的电话号码"));
		} else {
			callback();
		}
	}
	//匹配邮箱
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error("请输入正确的邮箱地址"));
		} else {
			callback();
		}
	}
	//匹配身份证
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error("请输入正确的身份证号码"));
		} else {
			callback();
		}
	}
	//匹配网站地址
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("请输入正确的URL地址"));
		} else {
			callback();
		}
	}
	const rules = ref({
		fangjianmingcheng: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		fangjiantupian: [
		],
		fangjianleixing: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		yiwanjiage: [
			{required: true,message: '请输入',trigger: 'blur'}, 
			{ validator: validateNumber, trigger: 'blur' },
		],
		fuwudianhua: [
			{ validator: validatePhone, trigger: 'blur' },
		],
		fangneisheshi: [
		],
		storeupnum: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		minsudizhi: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		minsuleixing: [
		],
		shangjiazhanghao: [
		],
		shangjiamingcheng: [
		],
		fangyuanzhuangtai: [
		],
	})
	//房间图片上传回调
	const fangjiantupianUploadSuccess=(e)=>{
		form.value.fangjiantupian = e
	}
	//房间类型列表
	const fangjianleixingLists = ref([])
	//民宿类型列表
	const minsuleixingLists = ref([])
	//房源状态列表
	const fangyuanzhuangtaiLists = ref([])
	//methods

	//methods
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			res.data.data.fangneisheshi = res.data.data.fangneisheshi.replace(reg,'../../../cl26000159/file');
			form.value = res.data.data
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init = (formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null) => {
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			// getInfo()
			for(let x in row){
				if(x=='fangjianmingcheng'){
					form.value.fangjianmingcheng = row[x];
					disabledForm.value.fangjianmingcheng = true;
					continue;
				}
				if(x=='fangjiantupian'){
					form.value.fangjiantupian = row[x];
					disabledForm.value.fangjiantupian = true;
					continue;
				}
				if(x=='fangjianleixing'){
					form.value.fangjianleixing = row[x];
					disabledForm.value.fangjianleixing = true;
					continue;
				}
				if(x=='yiwanjiage'){
					form.value.yiwanjiage = row[x];
					disabledForm.value.yiwanjiage = true;
					continue;
				}
				if(x=='fuwudianhua'){
					form.value.fuwudianhua = row[x];
					disabledForm.value.fuwudianhua = true;
					continue;
				}
				if(x=='fangneisheshi'){
					form.value.fangneisheshi = row[x];
					disabledForm.value.fangneisheshi = true;
					continue;
				}
				if(x=='storeupnum'){
					form.value.storeupnum = row[x];
					disabledForm.value.storeupnum = true;
					continue;
				}
				if(x=='minsudizhi'){
					form.value.minsudizhi = row[x];
					disabledForm.value.minsudizhi = true;
					continue;
				}
				if(x=='minsuleixing'){
					form.value.minsuleixing = row[x];
					disabledForm.value.minsuleixing = true;
					continue;
				}
				if(x=='shangjiazhanghao'){
					form.value.shangjiazhanghao = row[x];
					disabledForm.value.shangjiazhanghao = true;
					continue;
				}
				if(x=='shangjiamingcheng'){
					form.value.shangjiamingcheng = row[x];
					disabledForm.value.shangjiamingcheng = true;
					continue;
				}
				if(x=='fangyuanzhuangtai'){
					form.value.fangyuanzhuangtai = row[x];
					disabledForm.value.fangyuanzhuangtai = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			form.value.storeupnum='0'
			form.value.fangyuanzhuangtai='空闲'
		}
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('shangjiazhanghao') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.shangjiazhanghao = json.shangjiazhanghao
				disabledForm.value.shangjiazhanghao = true;
			}
			if(json.hasOwnProperty('shangjiamingcheng') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.shangjiamingcheng = json.shangjiamingcheng
				disabledForm.value.shangjiamingcheng = true;
			}
		})
		fangjianleixingLists.value = "单人间,大床房,双人间,三人间,标准套间,豪华套间".split(',')
		context?.$http({
			url: `option/minsuleixing/minsuleixing`,
			method: 'get'
		}).then(res=>{
			minsuleixingLists.value = res.data.data
		})
		fangyuanzhuangtaiLists.value = "已预订,空闲".split(',')
	}
	//初始化
	//取消
	const backClick = () => {
		history.back()
	}
	//富文本数据回调
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//提交
	const save=()=>{
		if(form.value.fangjiantupian!=null) {
			form.value.fangjiantupian = form.value.fangjiantupian.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(`操作成功`,'success',()=>{
									history.back()
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(`操作成功`,'success',()=>{
							history.back()
						})
					})
				}
			}
		})
	}
	//修改跨表数据
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
	onMounted(()=>{
		type.value = route.query.type?route.query.type:'add'
		let row = null
		let table = null
		let statusColumnName = null
		let tips = null
		let statusColumnValue = null
		if(type.value == 'cross'){
			row = context?.$toolUtil.storageGet('crossObj')?JSON.parse(context?.$toolUtil.storageGet('crossObj')):{}
			table = context?.$toolUtil.storageGet('crossTable')
			statusColumnName = context?.$toolUtil.storageGet('crossStatusColumnName')
			tips = context?.$toolUtil.storageGet('crossTips')
			statusColumnValue = context?.$toolUtil.storageGet('crossStatusColumnValue')
		}
		init(route.query.id?route.query.id:null, type.value,'', row, table, statusColumnName, tips, statusColumnValue)
	})
	
</script>
<style lang="scss" scoped>
	// 面包屑盒子
	.bread_view {
		border-radius: 0px;
		padding: 12px 20px;
		margin: 0px auto;
		background: none;
		width: 100%;
		border-color: #eee;
		border-width: 0 0 0px;
		position: relative;
		border-style: solid;
		:deep(.breadcrumb) {
			font-size: 14px;
			line-height: 1;
			.el-breadcrumb__separator {
				margin: 0 9px;
				color: #999;
				font-weight: 500;
			}
			.first_breadcrumb {
				.el-breadcrumb__inner {
					color: #333;
					display: inline-block;
				}
			}
			.second_breadcrumb {
				.el-breadcrumb__inner {
					color: #999;
					display: inline-block;
				}
			}
		}
	}
	// 表单
	.add_form{
		border: 1px solid #B5D0AC20;
		border-radius: 0px;
		padding: 40px 7% 50px;
		background: #B5D0AC10;
		// form item
		:deep(.el-form-item) {
			border: 0px solid #eee;
			padding: 6px 0;
			margin: 0 0 20px 0;
			background: none;
			display: flex;
			//label
			.el-form-item__label {
			 background: none;
			 display: block;
			 width: auto;
			 min-width: 150px;
			 text-align: right;
			}
			// 内容盒子
			.el-form-item__content {
				display: flex;
				width: calc(100% - 150px);
				justify-content: flex-start;
				align-items: center;
				flex-wrap: wrap;
				// 输入框
				.list_inp {
					padding: 0 10px;
					background: #fff;
					width: auto;
					border-color: #ccc;
					border-width: 1px;
					line-height: 36px;
					box-sizing: border-box;
					border-style: solid;
					min-width: 100%;
					height: 36px;
					//去掉默认样式
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
				// 下拉框
				.list_sel {
					border-radius: 0px;
					padding: 0 10px;
					background: #fff;
					width: auto;
					border-color: #ccc;
					border-width: 1px;
					line-height: 36px;
					box-sizing: border-box;
					border-style: solid;
					min-width: 100%;
					//去掉默认样式
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
							.el-input__wrapper{
								border: none;
								box-shadow: none;
								background: none;
								border-radius: 0;
								height: 100%;
								padding: 0;
							}
							.is-focus {
								box-shadow: none !important;
							}
						}
					}
				}
